<template>
  <div>
    <gmap-map v-if="center" :center="center" :zoom="13" :options="options" style="width:100%;  height: 250px;">
      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        @click="center=m.position"
      ></gmap-marker>
    </gmap-map>
  </div>
</template>

